<template><section><html><head></head><body><h1>GeoJsonDataSource</h1>
<p><code v-pre="">vc-datasource-geojson</code> 组件用于加载 <a href="https://geojson.org/">GeoJSON</a> 和 <a href="https://github.com/topojson/topojson">TopoJSON</a> 格式的数据。</p>
<h2>示例</h2>
<h3>加载 GeoJSON 格式数据源</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-datasource-geojson data="./statics/SampleData/lineData/streamline.json" @ready="subReady" :show="show" :options="options" ref="ds" @click="clicked" :entities="entities"></vc-datasource-geojson>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
<h4>代码</h4>
<pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"viewer"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span> <span class="hljs-attr">@ready</span>=<span class="hljs-string">"ready"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-datasource-geojson</span>
        <span class="hljs-attr">data</span>=<span class="hljs-string">"./statics/SampleData/lineData/streamline.json"</span>
        <span class="hljs-attr">@ready</span>=<span class="hljs-string">"subReady"</span>
        <span class="hljs-attr">:show</span>=<span class="hljs-string">"show"</span>
        <span class="hljs-attr">:options</span>=<span class="hljs-string">"options"</span>
        <span class="hljs-attr">ref</span>=<span class="hljs-string">"ds"</span>
        <span class="hljs-attr">@click</span>=<span class="hljs-string">"clicked"</span>
        <span class="hljs-attr">:entities</span>=<span class="hljs-string">"entities"</span>
      &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-datasource-geojson</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">vc-viewer</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="language-javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-title function_">data</span>(<span class="hljs-params"></span>) {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">show</span>: <span class="hljs-literal">true</span>,
        <span class="hljs-attr">options</span>: {
          <span class="hljs-attr">stroke</span>: <span class="hljs-string">'red'</span>
        },
        <span class="hljs-attr">entities</span>: []
      }
    },
    <span class="hljs-attr">methods</span>: {
      <span class="hljs-title function_">ready</span>(<span class="hljs-params">cesiumInstance</span>) {
        <span class="hljs-keyword">const</span> { <span class="hljs-title class_">Cesium</span>, viewer } = cesiumInstance
        <span class="hljs-keyword">for</span> (<span class="hljs-keyword">let</span> i = <span class="hljs-number">0</span>; i &lt; <span class="hljs-number">100</span>; i++) {
          <span class="hljs-variable language_">this</span>.<span class="hljs-property">entities</span>.<span class="hljs-title function_">push</span>({
            <span class="hljs-attr">position</span>: { <span class="hljs-attr">lng</span>: <span class="hljs-title class_">Math</span>.<span class="hljs-title function_">random</span>() * <span class="hljs-number">40</span> + <span class="hljs-number">85</span>, <span class="hljs-attr">lat</span>: <span class="hljs-title class_">Math</span>.<span class="hljs-title function_">random</span>() * <span class="hljs-number">30</span> + <span class="hljs-number">21</span> },
            <span class="hljs-attr">label</span>: {
              <span class="hljs-attr">text</span>: i.<span class="hljs-title function_">toString</span>(),
              <span class="hljs-attr">pixelOffset</span>: { <span class="hljs-attr">x</span>: <span class="hljs-number">25</span>, <span class="hljs-attr">y</span>: <span class="hljs-number">20</span> }
            },
            <span class="hljs-attr">point</span>: {
              <span class="hljs-attr">pixelSize</span>: <span class="hljs-number">8</span>,
              <span class="hljs-attr">outlineWidth</span>: <span class="hljs-number">2</span>,
              <span class="hljs-attr">color</span>: <span class="hljs-string">'red'</span>,
              <span class="hljs-attr">outlineColor</span>: <span class="hljs-string">'yellow'</span>
            }
          })
        }
      },
      <span class="hljs-title function_">subReady</span>(<span class="hljs-params">cesiumInstance</span>) {
        cesiumInstance.<span class="hljs-property">viewer</span>.<span class="hljs-title function_">zoomTo</span>(cesiumInstance.<span class="hljs-property">cesiumObject</span>)
      },
      <span class="hljs-title function_">clicked</span>(<span class="hljs-params">e</span>) {
        <span class="hljs-variable language_">console</span>.<span class="hljs-title function_">log</span>(e)
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<h2>属性</h2>
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>data</td>
<td>String|Object</td>
<td></td>
<td><code v-pre="">required</code> 指定要加载的 GeoJSON 或者 TopoJSON 的 url。</td>
</tr>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> 指定数据源是否显示。</td>
</tr>
<tr>
<td>entities</td>
<td>Array</td>
<td><code v-pre="">[]</code></td>
<td><code v-pre="">optional</code>指定要添加到该数据源的实体集合。</td>
</tr>
<tr>
<td>options</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定以下属性：</td>
</tr>
<tr>
<td>-------------</td>
<td>---------------------</td>
<td>-------</td>
<td>------------------------------------------------------</td>
</tr>
<tr>
<td>sourceUri</td>
<td>String</td>
<td></td>
<td></td>
</tr>
<tr>
<td>markerSize</td>
<td>Number</td>
<td></td>
<td>点大小。</td>
</tr>
<tr>
<td>markerSymbol</td>
<td>String</td>
<td></td>
<td>点风格。</td>
</tr>
<tr>
<td>markerColor</td>
<td>String|Object|Array</td>
<td></td>
<td>点颜色。</td>
</tr>
<tr>
<td>stroke</td>
<td>String|Object|Array</td>
<td></td>
<td>线、面轮廓颜色。</td>
</tr>
<tr>
<td>strokeWidth</td>
<td>Number</td>
<td></td>
<td>线、面轮廓宽度。</td>
</tr>
<tr>
<td>fill</td>
<td>String|Object|Array</td>
<td></td>
<td>面填充色。</td>
</tr>
<tr>
<td>clampToGround</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
<td>是否贴地。</td>
</tr>
<tr>
<td>credit</td>
<td>String|Object</td>
<td></td>
<td>版权信息。</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>参考官方文档： <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/GeoJsonDataSource.html">GeoJsonDataSource</a></strong></li>
</ul>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
<tr>
<td>changedEvent</td>
<td></td>
<td>数据源改变时触发。</td>
</tr>
<tr>
<td>errorEvent</td>
<td></td>
<td>数据源发生错误时触发。</td>
</tr>
<tr>
<td>loadingEvent</td>
<td></td>
<td>数据源开始或结束加载时触发。</td>
</tr>
<tr>
<td>clusterEvent</td>
<td>(clusteredEntities, cluster)</td>
<td>数据源聚合事件。</td>
</tr>
<tr>
<td>collectionChanged</td>
<td>(collection, added, removed, changed)</td>
<td>数据源实体集合改变时触发。</td>
</tr>
<tr>
<td>mousedown</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标在该数据源上按下时触发。</td>
</tr>
<tr>
<td>mouseup</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标在该数据源上弹起时触发。</td>
</tr>
<tr>
<td>click</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标单击该数据源时触发。</td>
</tr>
<tr>
<td>clickout</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标单击该数据源外部时触。</td>
</tr>
<tr>
<td>dblclick</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标左键双击该数据源时触发。</td>
</tr>
<tr>
<td>mousemove</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标在该数据源上移动时触发。</td>
</tr>
<tr>
<td>mouseover</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标移动到该数据源时触发。</td>
</tr>
<tr>
<td>mouseout</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标移出该数据源时触发。</td>
</tr>
</tbody>
</table>
<hr>
</body></html></section></template>

<script>
    export default {
      data() {
        return {
          show: true,
          options: {
            stroke: 'red'
          },
          entities: []
        }
      },
      methods: {
        ready(cesiumInstance) {
          this.cesiumInstance = cesiumInstance
          const { Cesium, viewer } = cesiumInstance
          for (let i = 0; i < 100; i++) {
            this.entities.push({
              position: { lng: Math.random() * 40 + 85, lat: Math.random() * 30 + 21 },
              label: {
                text: i.toString(),
                pixelOffset: {x: 25, y: 20}
              },
              point: {
                pixelSize: 8,
                outlineWidth: 2,
                color: 'red',
                outlineColor: 'yellow'
              }
            })
          }
        },
        subReady(cesiumInstance) {
          cesiumInstance.viewer.zoomTo(cesiumInstance.cesiumObject)
        },
        clicked (e) {
          console.log(e)
        }
      }
    }
  </script>